import React, { useEffect } from 'react'
import $ from 'jquery';
import './skills.css'
export default function Skills() {
    useEffect(()=>{
        $('.skills').slideDown()
      },[]);
    let skills=[{
        track:'FrontEnd',
        list:[{
            title:"HTML",
            logo:'html.png'
        },{
            title:"CSS",
            logo:'css.png'
        },{
            title:"JavaScript",
            logo:'js.png'
        },{
            title:"JQuery",
            logo:'jquery.png'
        },{
            title:"Bootstrap",
            logo:'bootstrap.png'
        },{
            title:"AJAX",
            logo:'ajax.png'
        },{
            title:"SASS",
            logo:'sass.png'
        },{
            title:"React",
            logo:'react.png'
        }]},{
        track:'BackEnd',
        list:[{
            title:"Node.js",
            logo:'nodejs.png'
        },{
            title:"Express.js",
            logo:'express.png'
        },{
            title:"SQL",
            logo:'sql.png'
        },{
            title:"Mongoose",
            logo:'mongoose.png'
        },{
            title:"Web Socket",
            logo:'websocket.png'
        },{
            title:"REST API",
            logo:'api.png'
        }]},{
        track:'Programming',
        list:[{
            title:"C++",
            logo:'c-.png'
        },{
            title:"Data Structure",
            logo:'algorithm.png'
        },{
            title:"OOP",
            logo:'programming.png'
        }]}];

function importAll(r) {
    let images = {};
    r.keys().map((item, index) => { images[item.replace('./', '')] = r(item); });
    return images;
  }
  const images = importAll(require.context('../../images/skills', false, /\.(png)$/));
  return <>
    <div className='slide-container skills'>
    <h2 className='mb-3'>Skills</h2>
    <div className='row'>
        {skills.map((skill,id)=>{
            return <div key={id} className='col-md-4 mb-3'>
            <div>
                <h5>{skill.track}</h5>
                <ul>
                    {skill.list.map((item,index)=>{
                        return <li key={index}> <img className='me-2 bg-white' src={images[item.logo]}/>{item.title}</li>
                    })}
                    
                </ul>
           
    
            </div>
          </div>
        })}
      
        

        </div>

    </div>
  </>
}
