import React, { useEffect, useRef, useState } from 'react';
import $ from 'jquery';
import './contact.css';
import ContactIcons from './../ContactIcons/ContactIcons';
import emailjs from '@emailjs/browser';
import { faCheck,faXmark } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
export default function Contact() {
  const form = useRef();
  useEffect(()=>{
    $('.contact').slideDown()
  },[]);
  let [err,setErr]=useState(null);

  function handleSubmit(e){
    setErr(null);
    e.preventDefault();
    emailjs.sendForm("service_3jbv3m2", "template_qvb4zy4", form.current ,'di_mWn7Hln51R9pde').then(
      (result) => {
        setErr(false);
        let inputs = document.querySelectorAll("input");
        inputs.forEach((input) => (input.value = ""));
        document.querySelector("textarea").value='';
      },
      (error) => {
        setErr(true);
      }
    );
  }
  return <>
    <div className='slide-container contact'>
    <h2 className='mb-3'>Contact Me or Send Me Your Feedback</h2>
    <form onSubmit={handleSubmit} ref={form}>
    <input type="text" placeholder='Name' name='name' required />
       <input type="email" placeholder='email' name='email' required />
       <textarea required placeholder='Your Message' name='message' rows={7}></textarea>
       
       {err!==null?(err?<span className='err-msg text-danger'><FontAwesomeIcon className='me-1' icon={faXmark}/>error</span>:
      <span className='err-msg text-success'><FontAwesomeIcon className='me-1' icon={faCheck}/>success</span> ):''
      }
       <div className='row gy-5'>
          <div className='col-lg-5'>
            <button className='orange-btn' type='submit' >Send Message</button>
          </div>
          <div className='col-lg-7 d-flex d-block d-lg-none '>
          <ContactIcons/>
          </div>
       </div>
    </form>
      

       
      
    </div>
  </>
}
