import React, { useState } from 'react';
import './slider.css';
import About from './../About/About';
import Home from '../Home/Home';
import Contact from '../Contact/Contact';
import Portfolio from '../Portfolio/Portfolio';

import ContactIcons from '../ContactIcons/ContactIcons';
import Skills from '../Skills/Skills';

export default function Slider() {

    let [currentSlide,setCurrentSlide]=useState(<Home/>);

    let slides=[<Home/>,<About/>,<Skills/>,<Portfolio/>,<Contact/>];

    function swipSlide(slide){
        setCurrentSlide(slide);
    };
  return <>
   <div className='main-bg'>
    <div className='overlay'>
        <div className='col-md-8 col-10'>{currentSlide}</div>
       
    <div className='slider-dots'>
    {slides.map((slide,id)=>{
        return <div key={id} onClick={()=>swipSlide(slide)} className='dot'></div>
    })}
  </div>

  <div className='connect d-none d-lg-block'>
    <p className='connect-title'>Let's Get Connected</p>
   
    </div>
    <div className='icons d-none d-lg-block'>
        <ContactIcons/> 
    </div>
  
    </div>
    </div>
 
 
  </>
}
