import React, { useEffect } from 'react';
import $ from 'jquery';
import './home.css';

export default function Home() {
  function download() {
    fetch('Gehad Hisham Shaddad.pdf').then(response => {
        response.blob().then(blob => {
            const fileURL = window.URL.createObjectURL(blob);
            let alink = document.createElement('a');
            alink.href = fileURL;
            alink.download = 'Gehad Hisham Shaddad.pdf';
            alink.click();
        })
    })
};
useEffect(()=>{
  $('.home').slideDown()
},[]);
  return <>
      <div className='home'>
        <h1>Gehad Hisham Shaddad</h1>
        <button onClick={download} className='orange-btn'>Download CV</button>
      
      </div>

  </>
}
