import React, { useEffect } from 'react';
import './about.css';
import $ from 'jquery';

export default function About() {
  useEffect(()=>{
    $('.about').slideDown();
  },[]);
  return <>
    <div className='about slide-container '>
        <h2 className='mb-3'>About Me</h2>
        <p>Im a front-end and back-end developer with experience in building websites for small and medium sized businesses. Whether youre trying to win work, list your services or even create a whole online store  I can help! <br/> Ill fully project manage your brief from start to finish Regular communication is really important to me, so lets keep in touch!</p>
        <div className='line'></div>
        <div className='info row'>
            <div className='col-lg-6'>
                <p> <span>Name: </span>Gehad Hisham Shaddad</p>
                <p><span>Phone: </span>+20 01202425522</p>
                <p><span>University: </span> Faculty of Engineering, Ain Shams Univercity</p>
            </div>
            <div className='col-lg-6'>
            <p><span>Age: </span>23</p>
                <p><span>Location: </span>El Shorouk, Cairo, Egypt</p>
                <p><span>Education:  </span>Electrical Engineering, Computer and System Engineering</p>
            </div>
        </div>
    </div>
  </>
}
