import React from 'react';
import './contactIcons.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faU } from '@fortawesome/free-solid-svg-icons';
import { faFacebook, faGithub, faGoogle, faLinkedin, faWhatsapp } from "@fortawesome/free-brands-svg-icons";
export default function ContactIcons() {
    let icons=[{
        name:faFacebook,
        link:'https://www.facebook.com/boga.hesham/'
    },{
        name:faWhatsapp,
        link:'https://wa.me/201202425522'
    },{
        name:faGithub,
        link:'https://github.com/GehadHisham20'
    },{
        name:faGoogle,
        link:'mailto:gehadhisham856@gmail.com'
    },{
        name:faLinkedin,
        link:'https://www.linkedin.com/in/gehadhisham'
    },{
        name:faU,
        link:'https://www.upwork.com/freelancers/~0146cdcba93f5fcdb8'
    }];
  return <>
  {icons.map((icon,id)=>{
            return <a className='icon' rel="noreferrer"  key={id} href={icon.link} target='_blank'><FontAwesomeIcon  size='xl' icon={icon.name} /></a>;
        })}
  </>
}
