import React, { useEffect } from 'react';
import './portfolio.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLink } from '@fortawesome/free-solid-svg-icons';
import { faGithub } from "@fortawesome/free-brands-svg-icons";
import $ from 'jquery';
export default function Portfolio() {

  useEffect(()=>{
    $('.portfolio').slideDown()
  },[]);

  let projects = [{
      image: "book.PNG",
      title: "Book App",
      desc: `MERN App `,
      githubLink: "https://github.com/GehadHisham20/book-app",
      websiteLink: "https://my-bookish-app.herokuapp.com/",
    },{
      image: "note.PNG",
      title: "Notes App",
      desc: `MERN App `,
      githubLink: "https://github.com/GehadHisham20/Note-App",
      websiteLink: "https://my-note-app-demo.herokuapp.com/",
    },{
      image: "angora.PNG",
      title: "Angora",
      desc: "Web Design",
      githubLink: "https://github.com/GehadHisham20/Angora-portfolio",
      websiteLink: "https://gehadhisham20.github.io/Angora-portfolio/",
    },{
      image: "login.PNG",
      title: "Login System",
      desc: "JavaScript App",
      githubLink: "https://github.com/GehadHisham20/Smart-Login-System",
      websiteLink: "https://gehadhisham20.github.io/Smart-Login-System/",
    },{
      image: "morgan.PNG",
      title: "Morgan",
      desc: "Web Design",
      githubLink: "https://github.com/GehadHisham20/Morgan-devfolio",
      websiteLink: "https://gehadhisham20.github.io/Morgan-devfolio/",
    },{
      image: "bookmarker.PNG",
      title: "Bookmarker",
      desc: "JavaScript App",
      githubLink: "https://github.com/GehadHisham20/Bookmarker",
      websiteLink: "https://gehadhisham20.github.io/Bookmarker/",
    },{
      image: "sanitary.PNG",
      title: "Sanitary",
      desc: "Web Design",
      githubLink: "https://github.com/GehadHisham20/sanitary-home-page",
      websiteLink: "https://gehadhisham20.github.io/sanitary-home-page/",
    },{
      image: "weather.PNG",
      title: "Weather",
      desc: `Web App`,
      githubLink: "https://github.com/GehadHisham20/Weather-App",
      websiteLink: "https://youtu.be/Oo99JoxwuJM",
    }];

    function importAll(r) {
      let images = {};
      r.keys().map((item, index) => { images[item.replace('./', '')] = r(item); });
      return images;
    }
    const images = importAll(require.context('../../images/projects', false, /\.(PNG|jpe?g|svg)$/));
  return<>
  <div className='slide-container portfolio'>
    <div className='row justify-content-center justify-content-xl-start '>
    <h2 className='mb-3'>Portfolio</h2>
        {projects.map((project,id)=>{
          return <div className="col-xl-4 col-lg-6 col-sm-9" key={id}>
            <div className='project-card'>
              <img className='img-fluid' src={images[project.image]} alt={project.title} />
              <div className="project-overlay">
                <div className='overlay-content'>
                  <h6>{project.title}</h6>
                  <p className='d-none d-sm-block'>{project.desc}</p>
                  <a rel="noreferrer" className='mx-1 web-icon' href={project.githubLink} target="_blank"><FontAwesomeIcon  icon={faGithub} /></a>
                  <a rel="noreferrer" className='mx-1 web-icon'href={project.websiteLink} target="_blank"><FontAwesomeIcon  icon={faLink} /></a>
                  
                </div>
                 
              </div>
            </div>
          </div>
        })}
    </div>
     
    </div>
  </>
}
